.films {
  width: calc(100% - 40px);
  min-width: 320px;
  min-height: calc(100vh - 450px);
  margin: 20px 20px 40px;
  list-style: none;
  display: grid;
}

.films__card {
  margin-bottom: 20px;
}

.films__poster {
  width: 100%;
  border-radius: 5px;
}

.films__title {
  text-transform: uppercase;
  margin-top: 10px;
  margin-bottom: 5px;
}

.films__details {
  color: #ff6b08;
  margin-top: 5px;
  margin-right: 10px;
  display: inline;
}

.films__rate {
  color: #fff;
  background-color: #ff6b08;
  border-radius: 5px;
  padding: 1px 10px;
  display: inline;
}

@media screen and (min-width: 560px) and (max-width: 1023px) {
  .films {
    width: calc(100% - 152px);
    min-height: calc(100vh - 500px);
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin: 60px 76px;
  }
}

@media screen and (min-width: 1022px) {
  .films {
    max-width: 1024px;
    min-height: calc(100vh - 385px);
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    margin: auto;
    padding: 60px 71px;
  }
}

