  height: 120px;
  background-color: rgba(80, 30, 2, .3);
  border: 16px solid rgba(56, 34, 24, .34);
  border-top-color: rgba(255, 107, 8, .65);
  border-radius: 50%;
  margin: 50px auto 0;
